"use client";

import React, { createContext, useContext, useMemo, useState } from "react";
import { ENABLE_LANGUAGE_FEATURE } from "@/config/feature-flags";

export interface LanguageContextValue {
  language: string;
  setLanguage: (lang: string) => void;
  supported: { code: string; label: string }[];
}

const LanguageContext = createContext<LanguageContextValue | undefined>(undefined);

const SUPPORTED_LANGUAGES: { code: string; label: string }[] = [
  { code: "en", label: "English" },
  { code: "de", label: "Deutsch" },
  { code: "fr", label: "Franais" },
  { code: "es", label: "Espaol" },
  { code: "pt", label: "Portugus" },
  { code: "it", label: "Italiano" },
  { code: "ru", label: "" },
  { code: "zh", label: "" },
  { code: "ja", label: "" },
  { code: "ko", label: "" },
  { code: "pl", label: "Polski" },
  { code: "cs", label: "etina" },
  { code: "hu", label: "Magyar" },
  { code: "ro", label: "Romn" },
  { code: "sk", label: "Slovenina" },
  { code: "tr", label: "Trke" },
];

export function LanguageProvider({ children }: { children: React.ReactNode }) {
  const [language, setLanguageState] = useState<string>(() => {
    if (!ENABLE_LANGUAGE_FEATURE) return "en";
    if (typeof window !== "undefined") {
      const saved = localStorage.getItem("language");
      return saved || "en";
    }
    return "en";
  });

  const setLanguage = (lang: string) => {
    if (!ENABLE_LANGUAGE_FEATURE) {
      setLanguageState("en");
      return;
    }
    setLanguageState(lang);
    if (typeof window !== "undefined") {
      localStorage.setItem("language", lang);
    }
  };

  const value = useMemo<LanguageContextValue>(() => ({
    language,
    setLanguage,
    supported: SUPPORTED_LANGUAGES,
  }), [language]);

  return (
    <LanguageContext.Provider value={value}>{children}</LanguageContext.Provider>
  );
}

export function useLanguage(): LanguageContextValue {
  const ctx = useContext(LanguageContext);
  if (!ctx) throw new Error("useLanguage must be used within a LanguageProvider");
  return ctx;
}
